@import url('https://fonts.googleapis.com/css2?family=Lora&family=Open+Sans:wght@700,400&display=swap');

* { 
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}

#app {
  display: flex;
  flex-flow: column;
  width: 60%;
  margin: 5em;
}

h1 {
  text-transform: uppercase;  
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 4em;
}

ul {
  list-style-type: none;
  font-size: 1.5em;
  margin-left: auto;
  text-align: right;
}

li {
  margin: 0.75em 0;
}

li:nth-child(odd) {
  transform: rotate(3deg);
}

li:nth-child(even) {
  transform: rotate(-3deg);
}

a {
  text-decoration: none;
  color: inherit;
  transition: color 0.3s ease;
}

a:hover {
  color: blue;
}